import Board from '@/components/Board.vue'
import { mapState, mapActions, mapMutations } from 'vuex'

export default {
  name: 'GuessGame',
  components: { Board },
  data() {
    return {
      loading: true,
      isCalculating: false,
      finished: false,
      questionCount: 1,
      totalScore: 0,
      currentGuess: '',
      lastGuess: null,
      actualScore: null,
      roundScore: 0,
      showResult: false,
      currentPosition: [],
      evaluating: false,
      engineReady: false,
      history: [],
      boardWidth: 0,
      resizeObserver: null
    }
  },
  computed: {
    ...mapState({
      aiReady: state => state.ai.ready,
      aiThinking: state => state.ai.thinking,
      aiOutput: state => state.ai.outputs
    }),
    displayGuess() {
      if (this.currentGuess === 'WIN') return ''
      if (this.currentGuess === 'LOSS') return ''
      return this.currentGuess
    },
    displayLastGuess() {
      if (this.lastGuess === 10000) return ''
      if (this.lastGuess === -10000) return ''
      return this.lastGuess
    },
    hasValidInput() {
      if (this.currentGuess === 'WIN' || this.currentGuess === 'LOSS') return true
      if (!this.currentGuess || this.currentGuess === '-') return false
      return true
    }
  },
  async mounted() {
    if (!this.aiReady) {
      await this.initEngine(true)
    }
    this.engineReady = true
    
    // Initialize ResizeObserver
    this.resizeObserver = new ResizeObserver(() => {
      this.updateBoardSize()
    })
    if (this.$refs.boardWrapper) {
      this.resizeObserver.observe(this.$refs.boardWrapper)
    }
    
    this.startNewGame()
  },
  beforeDestroy() {
    if (this.resizeObserver) {
      this.resizeObserver.disconnect()
    }
  },
  methods: {
    ...mapActions('ai', ['initEngine', 'think', 'stop']),
    ...mapMutations('position', { newGame: 'new', movePiece: 'move' }),
    ...mapMutations('settings', { setSetting: 'setValue' }),
    
    updateBoardSize() {
      if (!this.$refs.boardWrapper) return
      const width = this.$refs.boardWrapper.clientWidth
      const height = this.$refs.boardWrapper.clientHeight
      const size = Math.min(width, height) - 20 // 20px padding
      if (size > 0 && Math.abs(this.boardWidth - size) > 10) {
        this.boardWidth = size
      }
    },

    winRate(score) {
      return 1 / (1 + Math.exp(-score / 200));
    },

    scoreOf(guess, actual) {
      if (actual == null) return 0;
      const w1 = this.winRate(guess);
      const w2 = this.winRate(actual);
      const diff = Math.abs(w1 - w2);
      return Math.max(0, Math.round(200 * (0.5 - diff)));
    },

    formatScore(score) {
      if (score >= 10000) return ''
      if (score <= -10000) return ''
      return score
    },

    input(char) {
      if (this.currentGuess === 'WIN' || this.currentGuess === 'LOSS') {
        this.currentGuess = ''
      }
      if (char === '-') {
        if (this.currentGuess.length > 0) return
        this.currentGuess = '-'
        return
      }
      if (this.currentGuess === '0') this.currentGuess = ''
      
      let nextVal = this.currentGuess + char
      let num = parseInt(nextVal)
      if (!isNaN(num)) {
        if (num > 9999) nextVal = '9999'
        if (num < -9999) nextVal = '-9999'
      }
      this.currentGuess = nextVal
    },

    inputWin() {
      this.currentGuess = 'WIN'
    },

    inputLoss() {
      this.currentGuess = 'LOSS'
    },

    backspace() {
      if (this.currentGuess === 'WIN' || this.currentGuess === 'LOSS') {
        this.currentGuess = ''
      } else {
        this.currentGuess = this.currentGuess.slice(0, -1)
      }
    },

    async startNewGame() {
      this.finished = false
      this.totalScore = 0
      this.questionCount = 1
      this.history = []
      this.loading = true
      await this.generateNextQuestion()
      this.loading = false
      // Wait for DOM update then resize
      this.$nextTick(() => this.updateBoardSize())
    },

    restartGame() {
      this.startNewGame()
    },

    async generateNextQuestion() {
      this.loading = true
      this.showResult = false
      this.currentGuess = ''
      this.actualScore = null
      this.isCalculating = false
      
      let valid = false
      let position = []
      
      while (!valid) {
        position = this.generateRandomPosition()
        //const score = await this.getEngineEvaluation(position, 12, true)
        //if (Math.abs(score) < 500) {
          valid = true
        //}
      }
      
      if (!valid) {
        console.warn("Failed to generate balanced position")
      }

      this.currentPosition = position
      this.setupBoard(position)
      this.loading = false
    },

    generateRandomPosition() {
      const center = 7
      const pos = []
      const used = new Set()
      
      const addStone = (range) => {
        let r, c, key
        do {
          r = center + Math.floor(Math.random() * (range * 2 + 1)) - range
          c = center + Math.floor(Math.random() * (range * 2 + 1)) - range
          key = r + ',' + c
        } while (used.has(key) || r < 0 || r > 14 || c < 0 || c > 14)
        used.add(key)
        pos.push([r, c])
      }

      pos.push([center, center]); used.add(center + ',' + center)
      addStone(1); addStone(2); addStone(3); 
      addStone(4); addStone(4); addStone(4);

      return pos
    },

    setupBoard(position) {
      this.newGame(15)
      position.forEach(p => {
        this.movePiece(p)
      })
    },

    async getEngineEvaluation(position, depth, silent = false) {
      this.setupBoard(position)
      return this.runEngine(depth, silent)
    },

    async runEngine(depth, silent = false) {
      // Save original settings
      const originalShowAnalysis = this.$store.state.settings.showAnalysis
      const originalShowDetail = this.$store.state.settings.showDetail
      const originalShowPvEval = this.$store.state.settings.showPvEval
      
      if (silent) {
        this.setSetting({ key: 'showAnalysis', value: false })
        this.setSetting({ key: 'showDetail', value: false })
        this.setSetting({ key: 'showPvEval', value: 0 })
      } else {
        this.setSetting({ key: 'showPvEval', value: 3 })
      }

      this.setSetting({ key: 'thinkTimeOption', value: 0 })
      this.setSetting({ key: 'maxDepth', value: depth })
      this.setSetting({ key: 'threads', value: 1 })
      
      this.evaluating = true
      await this.think()
      return new Promise(resolve => {
        const check = () => {
           const pv = this.aiOutput && this.aiOutput.pv ? this.aiOutput.pv[0] : null
           const evalVal = this.parseEval(pv ? pv.eval : 0)
           if (!this.aiThinking) {
             finish(evalVal)
           } else if (Math.abs(evalVal) === 10000) {
             this.stop()
             finish(evalVal)
           } else if (pv && pv.depth >= depth) {
             this.stop()
             finish(evalVal)
           } else {
             setTimeout(check, 0)
           }
        }

        const finish = (val) => {
          this.setSetting({ key: 'showAnalysis', value: originalShowAnalysis })
          this.setSetting({ key: 'showDetail', value: originalShowDetail })
          this.setSetting({ key: 'showPvEval', value: originalShowPvEval })
          resolve(val)
        }

        check()
      })
    },

    parseEval(evalStr) {
      if (!evalStr) return 0
      evalStr = String(evalStr)
      if (evalStr.startsWith('+M')) return 10000
      if (evalStr.startsWith('-M')) return -10000
      let val = parseInt(evalStr) || 0
      if (val > 10000) val = 10000
      if (val < -10000) val = -10000
      return val
    },

    async submitGuess() {
      let guessVal
      if (this.currentGuess === 'WIN') guessVal = 10000
      else if (this.currentGuess === 'LOSS') guessVal = -10000
      else guessVal = parseInt(this.currentGuess)

      if (isNaN(guessVal)) return

      this.lastGuess = guessVal
      this.isCalculating = true
      
      this.actualScore = await this.getEngineEvaluation(this.currentPosition, 20)
      
      this.isCalculating = false
      this.roundScore = this.scoreOf(guessVal, this.actualScore)
      this.totalScore += this.roundScore
      
      this.history.push({
        guess: guessVal,
        actual: this.actualScore,
        score: this.roundScore
      })

      this.showResult = true
    },

    async nextQuestion() {
      if (this.questionCount >= 10) {
        this.finished = true
      } else {
        this.questionCount++
        await this.generateNextQuestion()
      }
    }
  }
}
