import { mapState } from 'vuex'
import { sendCommand } from '@/ai/engine'

export default {
  name: 'Console',
  data() {
    return {
      command: ''
    }
  },
  computed: {
    ...mapState({
      consoleLogs: state => state.ai.consoleLogs
    })
  },
  watch: {
    consoleLogs() {
      this.$nextTick(() => {
        const container = this.$refs.logContainer
        if (container) container.scrollTop = container.scrollHeight
      })
    }
  },
  methods: {
    send() {
      if (!this.command) return
      sendCommand(this.command)
      this.command = ''
    },
    getLogColor(type) {
      if (type === 'stdin') return '#0000FF'
      if (type === 'stderr') return '#FF0000'
      return '#333333'
    }
  }
}
